"use client";
import React, { useEffect, useState } from "react";

const Header = () => {
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  useEffect(() => {
    let menubtn = document.querySelector("#menubtn");
    let path1 = document.querySelector("#path1");
    let path2 = document.querySelector("#path2");

    let isMenuOpen = false;

    menubtn.addEventListener("click", function () {
      if (isMenuOpen) {
        gsap.to(path1, { attr: { d: "M 0 5 L 25 5" } });
        gsap.to(path2, { attr: { d: "M 0 13 L 25 13" } });
        gsap.to(".links a", {
          color: "#222",
          duration: 0,
          delay: 0.6,
        });
        gsap.to("#menubtn", {
          attr: {
            stroke: "#000",
          },
          delay: 0.6,
        });
        gsap.to("#cart", {
          attr: {
            stroke: "#000",
          },
          delay: 0.6,
        });
        gsap.to("#logoText", {
          attr: {
            src: "public/logo.svg",
          },
          delay: 0.6,
        });
        gsap.to("#menu", {
          height: 0,
          opacity: 0,
          duration: 0.3,
          delay: 0.5,
        });
        gsap.to(".primary a", {
          opacity: 0,
          transform: "translateX(40px)",
          duration: 0.2,
          stagger: 0.02,
          ease: "back.in",
        });
        gsap.to(".btns", {
          background: "#f1f1f1",
          delay: 0.7,
        });
        isMenuOpen = false;
      } else {
        gsap.to(path1, { attr: { d: "M 0 0 L 17 16" } });
        gsap.to(path2, { attr: { d: "M 0 16 L 17 1" } });
        gsap.to(".links a", {
          color: "#fff",
          duration: 0,
        });
        gsap.to("#menubtn", {
          attr: {
            stroke: "#fff",
          },
        });
        gsap.to("#cart", {
          attr: {
            stroke: "#fff",
          },
        });
        gsap.to("#menu", {
          height: "100vh",
          opacity: 1,
          duration: 0.3,
          delay: 0.3,
        });
        gsap.to("#logoText", {
          attr: {
            src: "public/logoWhite.svg",
          },
          duration: 0.3,
        });
        gsap.to(".primary a", {
          opacity: 1,
          transform: "translateX(0)",
          duration: 0.4,
          delay: 0.3,
          stagger: 0.05,
          ease: "back.in",
        });
        gsap.to(".btns", {
          background: "transparent",
        });
        isMenuOpen = true;
      }
    });
  }, []);


  return (
    <div className="header w-full flex items-center justify-center h-[20vh] fixed z-40">
      <nav className="w-[96%] flex items-center justify-between h-full">
        <div className="logo flex items-center justify-center relative overflow-hidden z-10">
          <img
            id="logoImg"
            src="/logo.svg"
            alt=""
            className="absolute w-[100px] bottom-[-120%] left-0"
          />
          <img id="logoText" src="/logo.svg" alt="" className="w-[125px]" />
        </div>

        <div className="cta flex items-start justify-center h-[70%] gap-[60px]">
          <div className="links flex items-start justify-center gap-[80px] py-[12px] z-20">
            <a
              href=""
              className="text-[#222] text-[14px] font-medium flex flex-col gap-[2px] hover:text-[#888] transition duration-500"
            >
              SHOP
            </a>
            <a
              href=""
              className="text-[#222] text-[14px] font-medium flex flex-col gap-[2px] hover:text-[#888] transition duration-500"
            >
              WHOLESALE
            </a>
            <a
              href=""
              className="text-[#222] text-[14px] font-medium flex flex-col gap-[2px] hover:text-[#888] transition duration-500"
            >
              CATERING
            </a>
            <a
              href=""
              className="text-[#222] text-[14px] font-medium flex flex-col gap-[2px] hover:text-[#888] transition duration-500"
            >
              DONATE
            </a>
          </div>

          <div
            onClick={toggleMenu}
            className="btns flex items-start justify-center gap-[60px] z-10 p-4 rounded-full bg-[#F1F1F1]"
          >
            <svg
              id="menubtn"
              width="24"
              height="18"
              className="stroke-black cursor-pointer"
            >
              <path id="path1" d="M 0 5 L 25 5" />
              <path id="path2" d="M 0 13 L 25 13" />
            </svg>
          </div>
        </div>
      </nav>

      <div
        id="menu"
        className={`${
          menuOpen ? "h-screen opacity-100" : "h-0 opacity-0"
        } absolute top-0 left-0 w-full bg-black flex items-end justify-center transition-all duration-500`}
      >
        <div className="secondary w-[56%] h-full flex items-end justify-between gap-10">
          <div className="main w-[30%] flex flex-col gap-[50px]">
            <h3 className="text-gray-500 text-[16px] font-light uppercase">
              Connect with us
            </h3>
            <div className="main-sub flex flex-col gap-[5px]">
              <a href="" className="text-white text-[17px] font-light">
                Facebook
              </a>
              <a href="" className="text-white text-[17px] font-light">
                Instagram
              </a>
              <a href="" className="text-white text-[17px] font-light">
                Twitter
              </a>
              <a href="" className="text-white text-[17px] font-light">
                LinkedIn
              </a>
              <a href="" className="text-white text-[17px] font-light">
                Youtube
              </a>
            </div>
          </div>
          <div className="main w-[30%] flex flex-col gap-[50px]">
            <h3 className="text-gray-500 text-[16px] font-light uppercase">
              Nitty Gritties
            </h3>
            <div className="main-sub flex flex-col gap-[5px]">
              <a href="" className="text-white text-[17px] font-light">
                Good Things FAQS
              </a>
              <a href="" className="text-white text-[17px] font-light">
                Good Food FAQS
              </a>
              <a href="" className="text-white text-[17px] font-light">
                Good Places
              </a>
            </div>
          </div>
          <div className="main w-[30%] flex flex-col gap-[50px]">
            <h3 className="text-gray-500 text-[16px] font-light uppercase">
              Get Started
            </h3>
            <div className="main-sub flex flex-col gap-[5px]">
              <a href="" className="text-white text-[17px] font-light">
                Pathways
              </a>
              <a href="" className="text-white text-[17px] font-light">
                Careers
              </a>
            </div>
          </div>
        </div>
        <div className="primary h-full w-[40%] flex justify-end flex-col text-right text-[4vw] leading-[4vw] font-extrabold pr-[.5vw] pb-[2vw]">
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            SHOP
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            WHOLESALE
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            CATERING
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            IMPACT
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            STORIES
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            ABOUT
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            CONTACT
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            DONATE
          </a>
          <a
            href=""
            className="opacity-0 transform translate-x-[40px] text-white font-extrabold"
          >
            SIGN IN
          </a>
        </div>
      </div>
    </div>
  );
};

export default Header;
